import React from 'react'
import { StyledLink } from './StyledLink'

export const ArticleTags: ({ tags }: { tags?: string[] }) => JSX.Element = ({
  tags,
}: {
  tags?: string[]
}): JSX.Element => (
  <p>
    Tags:{' '}
    {tags &&
      tags.map((tag: string, i: number) => (
        <span key={i}>
          <StyledLink
            to={`/tag/${tag.toLocaleLowerCase().replace(/\s/gi, '_')}`}
          >
            {tag}
          </StyledLink>
          {i + 1 < tags.length ? ', ' : ''}
        </span>
      ))}
  </p>
)
